.zantimes-hero {
  background-size: cover !important;
  background-position: center !important;
}
.zantimes-hero__wrapper {
  position: relative;
  align-items: center;
  min-height: 500px;
  color: #FFFFFF;
  z-index: 1;
}
.zantimes-hero__heading {
  font-weight: 700;
}
.zantimes-hero__tagline {
  margin-bottom: 1rem;
  font-weight: 700;
  color: #C7C1DD;
}
.zantimes-hero .btn-primary {
  margin-top: 1.5rem;
}
@media screen and (max-width: 767px) {
  .zantimes-hero {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .zantimes-hero__wrapper {
    min-height: 350px;
  }
}